          label.setObjectText('ItemProductName', this.labelType === 'qr'? item.text : item.text_limited);
          label.setObjectText('ItemQR', item.code);

          const serialText =
            this.labelType === 'qr'
              ? item.imei_sn
              : `${item.imei_sn} • ${item.id}`;
          label.setObjectText('ItemSerialNumber', serialText);

          if (this.labelType === 'qr') {
            label.setObjectText('ItemQuoteNumber', item.id);
            label.setObjectText('itemQuoteID', item.quote_id);
          }

          if (this.labelType === 'barcode') {
            label.setObjectText('ItemPrice', item.price);
          }

          console.log(`[LABEL PREVIEW]`, label.render());
          label.print(printer.name);
        }
      } catch (e) {
        alert(e.message || e)
      } finally {
        this.closeModal()
      }
