        var label = dymo.label.framework.openLabelXml(this.labelType === 'qr' ? labelXmlQr : labelXmlBarcode)
        
        // var labelSetBuilder = new dymo.label.framework.LabelSetBuilder()`
        for (let ix = 0; ix < this.data.length; ix++) {
          const item = this.data[ix]
          label.setObjectText('ItemProductName', this.labelType === 'qr' ? item.Text : item.LimitedText)
          label.setObjectText('ItemPrice', item.Price)
          const serialText =
            this.labelType === 'qr'
              ? item.Imei
              : `${item.Imei} • ${item.Code}`;
          label.setObjectText('ItemSerialNumber', serialText);

          if (this.labelType === 'qr') {
            label.setObjectText('ItemQuoteNumber', item.Code);
          }
          label.setObjectText('itemQR', item.Barcode)
          if (this.imageLabel != null) {
            var image = dymo.label.framework.loadImageAsPngBase64(
              this.imageLabel
            )
            label.setObjectText('Image', image)
          }
          console.log(`[LABEL #${ix + 1} PREVIEW] `, label.render())
          label.print(printer.name)
        }
      } catch (e) {
        alert(e.message || e)
      } finally {
        this.closeModal()
      }
    },
    callFunc(printer) {
      this[this.func](printer)
    },
    closeModal() {
      this.$emit('close-modal')
    }
  }
